.button-menu {
	width: 64px;
	height: 64px;
	border-radius: 50%;
	display: inline-grid;
	place-items: center;
	border: 1px solid var(--color-button-menu);
	fill: var(--color-button-menu);
	svg {
		transition: transform 0.5s ease;
	}
	&:focus-visible,
	&:hover {
		fill: var(--color-button-menu-hover);
		border-color: var(--color-button-menu-hover);
	}
	&:hover svg {
		transform: scale(1.1);
	}
}


.button-enter {
	margin-top: 4vh;
	cursor: not-allowed;
	transition: transform 0.5s ease;
	&:hover {
		transform: translateX(15px) rotate(-40deg);
	}
}

.button-close {
	&:focus-visible,
	&:hover {
		stroke: var(--color-button-close-hover);
	}
}


// OUR BUTTONS 
.button {
	border: none;
	text-align:center;
	cursor:pointer;
	position:relative;
	display: inline-block;
	overflow:hidden;
	padding: 10px 39px;
	line-height: 1em;
	display: inline-block;
	z-index: 1;
	box-shadow: inset 0px 0px 0 1px #007755;
	border-radius: 25px;
	background: none;

	font-family:arial;
	font-weight: bold;
	font-size:14px;
	color:#007755;
	text-transform: uppercase;
	text-decoration:none;
	transition:all .3s ease;
	
	span, a{
		z-index:2;
		position:relative;
	}
	&__circle {
	  width:150%;
	  height:150%;
	  bottom:-150%;
	  left: -25%;
	  border-top-left-radius: 100%;
	  border-top-right-radius: 100%;
	  background: #007755;
	  position:absolute;
	  transition:all .3s ease;
	  z-index:1;
	}
	
	&:hover .button__circle{
	  bottom:0;
	}
	&:hover a, &:hover span{
	  color:#fff;
	}
	&--white {
	  box-shadow: inset 0px 0px 0 1px white;
	  color: white;
	  span, a {
		  color: white;
	  }
	  .button__circle {
		  background: white;
	  }
	  &:hover a, &:hover span{
		color:var(--accent-color);
	  }
	}
  }
  

  

/* DEFAULT LINK STYLES  */
a {
	text-decoration: none;
	color: var(--color-link);
	outline: none;
	&:hover {
		color: var(--color-link-hover);
		outline: none;
	}
	
	/* Better focus styles from https://developer.mozilla.org/en-US/docs/Web/CSS/:focus-visible */
	&:focus {
		/* Provide a fallback style for browsers
		 that don't support :focus-visible */
		outline: none;
		background: lightgrey;
	}
	
	&:focus:not(:focus-visible) {
		/* Remove the focus indicator on mouse-focus for browsers
		 that do support :focus-visible */
		background: transparent;
	}
	
	&:focus-visible {
		/* Draw a very noticeable focus style for
		 keyboard-focus on browsers that do support
		 :focus-visible */
		outline: 2px solid #fff;
		background: transparent;
	}
}

